/*   mainFresh   */
h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 35px;
  padding-top: 27px;
  width: 700px;
  margin: 0 auto; }
  @media (max-width: 700px) {
    h2 {
      width: 340px; } }

.freshWrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  padding: 0 0;
  width: 700px;
  margin: 20px auto; }
  @media (max-width: 700px) {
    .freshWrapper {
      grid-template-columns: repeat(2, 1fr);
      width: 340px; } }
  .freshWrapper .freshUnit {
    width: 160px;
    height: 102px;
    border-radius: 15px;
    display: inline-block;
    position: relative;
    overflow: hidden; }
    .freshWrapper .freshUnit:after, .freshWrapper .freshUnit:before {
      box-sizing: border-box;
      position: absolute;
      width: 100%;
      padding: 20px;
      color: #fff;
      -webkit-transition: -webkit-transform .4s ease-out;
      transition: -webkit-transform .4s ease-out;
      transition: transform .4s ease-out;
      transition: transform .4s ease-out, -webkit-transform .4s ease-out; }
    .freshWrapper .freshUnit:after {
      content: attr(data-title);
      top: -10px;
      background: rgba(0, 0, 0, 0.4);
      font-size: 17px;
      font-weight: 300;
      text-align: center;
      -webkit-transform: translateY(-100%) scale(0.8);
              transform: translateY(-100%) scale(0.8); }
    .freshWrapper .freshUnit:before {
      content: attr(data-description) "…";
      top: 33%;
      height: 80%;
      background: rgba(107, 38, 68, 0.6);
      font-size: 17px;
      -webkit-transform: translateY(100%) scale(0.8);
              transform: translateY(100%) scale(0.8);
      text-align: center; }
    .freshWrapper .freshUnit:hover:after, .freshWrapper .freshUnit:hover:before {
      cursor: pointer;
      -webkit-transform: translateY(0%) scale(1);
              transform: translateY(0%) scale(1); }
    .freshWrapper .freshUnit_1 {
      background: url(/static/media/metro.229e5a12.jpg) no-repeat center;
      background-size: cover; }
    .freshWrapper .freshUnit_2 {
      background: url(/static/media/dota.3a4bc126.jpg) no-repeat center;
      background-size: cover; }
    .freshWrapper .freshUnit_3 {
      background: url(/static/media/genius.43fe1d57.jpg) no-repeat center;
      background-size: cover; }
    .freshWrapper .freshUnit_4 {
      background: url(/static/media/mtg.de28e9a2.jpg) no-repeat center;
      background-size: cover; }

